.input {
    display: block;
    border: none;
    color: whitesmoke;
    background: rgb(245,245,245, 0.4);
    height: 36px;
    width: 40px;
    text-align: center;
    margin: 10px;
    padding: 4px 0 0;
    font-size: 24px;
    font-family: 'Concert One', cursive;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.9s;
}

.input:hover {
    color: white;
    background: rgb(255,255,255);
    background: radial-gradient(circle, rgba(255,255,255,0.6362920168067228) 0%, rgba(255,255,255,0.5102415966386555) 100%);
    font-size: 30px;
}

