.App {
  top: 0;
  left: 0;
  margin: 5px;
  padding: 0;
  border-radius: 20px;
  width: 240px;
  height: 390px;
  background: rgb(0,113,224);
  background: linear-gradient(125deg, rgba(0,113,224,1) 30%, rgba(0,224,203,1) 99%);
}

.Display {
  background: rgb(38,50,56, 0.7);
  overflow: hidden;
  position: relative;
  top: 20px;
  height: 50px;
  margin: 0 20px 30px 20px;
  padding: 4px 10px 12px 10px;
  border: none;
  border-radius: 5px;
  font-size: 20px;
  font-weight: bold;
  color: whitesmoke;
  transition: all 0.7s;
  font-family: 'Audiowide', cursive;
}

.Display:hover {
  background: rgb(38,50,56, 0.9);
}

.Credit {
  text-align: center;
  font-family: 'Concert One', cursive;
  font-size: 20px;
  color: white;
  background: rgb(13,71,161);
  background: linear-gradient(90deg, rgba(13,71,161,1) 0%, rgba(3,169,244,1) 100%);
  padding: 10px 0px 15px 0px;
  border-radius: 0 0 20px 20px;
  cursor: pointer;
}